.img-container {
  position: relative;
  margin-top: 0px; 
  padding: 0; 
   min-height: 100%; 
   min-width: 1024px;

  width: 100%;
   height: auto;
}

.img-container img {
    width: 100% ;
    height: auto;
}

.img-container .btn {
    position: absolute;
    bottom: 29px;
    right: 23%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  background-color: rgb(74,20,140);
  color: white;
  font-size: 19px;
  padding:25px 100px 50px ;
  border: rgb(175,101,76) 1px solid;
  cursor: pointer;
  border-radius: 7px;
  text-align: center;
  box-shadow: cadetblue;
 }
  
.img-container .btn:hover {
    background-color: rgba(231, 169, 0, 0.986);
    color: black;
    
}
.centered {
